header{

    & nav{
        padding: 0.875rem 1.25rem 1.25rem 0.5rem;
        position: relative;
      }

     & .HH{
        display: block;
        width: 12rem;
    }



  

    & .header__menu{ //mobile menu

        & span{
            display: block;
            width:26px;
            height: 2px;
            background-color: $black;
            transition: all 300ms ease-in-out;
            transform-origin:3px 1px;
            


            &:not(:last-child){
                margin-bottom: 5px;
            }
        }

        &.open{
            
            > span:first-child{
              transform: rotate(45deg);  
            }
            
            > span:nth-child(2){
              opacity: 0;  
            }

            > span:last-child{
                transform: rotate(-45deg); 
            }
        }
       
    
    }



   


     
  & .header__links{
     position: relative;
      
      
     
     & a{
           color: $textblack;
           font-size: $font-md;
           font-weight: 550;
           
           :not(:nth-child(1),:nth-child(2)):hover{
            color: $lightgreen;
          } 
        
      }
        
    
    

     
    .header__dropdown{
        padding: 1rem;
    }
    
    .header__dropdown__payments {
        padding: 1rem;
         

    & .menu{
    display: none;
    position: absolute;
    top: 4rem;
    border-radius: 0.625rem;
    left: -4rem;
    width: 22rem;
    background-color: #ffffff;
    box-shadow: 0 10px 40px rgb(0 0 0 / 20%); 
   
    
    & .__cur{
        padding: 1rem;
      
        

        
    
        & .text {
        padding-left: 20px;
        
        }
    }
    

    }

  
  }

 


}

.header__dropdown__payments:hover .menu{
    display: block;
     transform: translateY(0); 
  }


 
  .header__dropdown__trade{
    padding: 1rem;

    & .menu{
    display: none;
    position: absolute;
    top: 4rem;
    border-radius: 0.625rem;
    left: 6.5rem;
    width: 22rem;
    background-color: #ffffff;
    box-shadow: 0 10px 40px rgb(0 0 0 / 20%); 
    
    & .__cur{
        padding: 1rem;
        transition: all 300ms ease-in-out;
        
    
        & .text {
        padding-left: 20px;
        }
    }
    

    }
  }


  .header__dropdown__trade:hover .menu{
    display: block;    
  }




  .header__btn{
padding: 0.875rem 2.8rem;
border-radius: 10px;
cursor: pointer;
border: none;
   }

   .header__btn:hover{
background-color: $darkblue;
color:$white;
   }

   
}


@include breakpoint-up(large){
  header{
    margin-left: 5rem;
    margin-right: 5rem;
  }
}